import React, { createContext, useState, useEffect } from 'react';
import axios from 'axios';
import { useRegions } from './hooks/useRegions';
import { useContacts } from './hooks/useContacts.js';
import { set } from 'lodash';

export const DataContext = createContext();

export const DataProvider = ({ children }) => {
    
    const [regions, setRegions] = useState([]);
    const [types, setTypes] = useState([]);
    const [statuses, setStatuses] = useState([]);
    const [titles, setTitles] = useState([]);
    const [activityTypes, setActivityTypes] = useState([]);
    const [activityPurposes, setActivityPurposes] = useState([]);
    const [namesEmails, setNamesEmails] = useState([]);
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [loading, setLoading] = useState(true);
    const tg = window.Telegram.WebApp;
    const params = new URLSearchParams(tg.initData);
    const chat_id = JSON.parse(params.get('user')).id;

    const contacts = useContacts(chat_id || null);
    const regionsWithComapnies = useRegions(chat_id || null);

   

    

    useEffect(() => {
        const fetchRegions = async () => {
        const params = {
            name: ' ',
            chatID: chat_id,
            api: 'getName'
        };
        try {
            const response = await axios.post(
                process.env.REACT_APP_GOOGLE_SHEETS_URL,
                JSON.stringify(params)
            );
            console.log('response Name', response.data);
            setName(response.data || '');
            setEmail(response.data.email || '');
            setRegions(response.data.regions || []);
            setNamesEmails(response.data.userNamesMails || []);
            
        } catch (error) {
            console.error('Error fetching regions:', error);
        }
    };
    const fetchTypesAndStatuses = async () => {
        console.log('fetchTypesAndStatuses');
        const params = {
            chatID: chat_id,
            api: 'getTypesAndStatuses'
        };
        try {
            const response = await axios.post(
                process.env.REACT_APP_GOOGLE_SHEETS_URL,
                JSON.stringify(params)
            );
            console.log('TS response', response.data);
            setTypes(response.data.types || []);
            setStatuses(response.data.statuses || []);
            setTitles(response.data.titles || []);
            setActivityPurposes(response.data.activityPurposes || []);
            setActivityTypes(response.data.activityTypes || []);
            
        } catch (error) {
            console.error('Error fetching types and statuses:', error);
        }
    };
        fetchRegions();
        fetchTypesAndStatuses();
        Promise.all([fetchRegions(), fetchTypesAndStatuses()]).then(() => {
            setLoading(false);
        });
    }, [chat_id]);

    useEffect(() => {
        if (loading) {
            tg.ready();
            tg.BackButton.hide();
        }
    }, [loading, tg]);
    console.log(activityPurposes, 'activityPurposes')
    return (
        <DataContext.Provider value={{
            loading,
            chat_id,
            name,
            email,
            regions,
            types,
            titles,
            statuses,
            activityTypes,
            activityPurposes,
            namesEmails
            
        }}>
            {children}
        </DataContext.Provider>
    );
};